import React, { useEffect, useRef, useState } from 'react';

const Slider1 = (props) => {
    const imgRef = useRef(null)
    const imgRef1 = useRef(null)
    const listRef = useRef(null)
    const [height, setHeight] = useState(0)
    const [height1, setHeight1] = useState(0)
    const imgLoaded = () => {
        setHeight(imgRef.current.offsetHeight);
    }

    const imgLoaded1 = () => {
        console.log(listRef.current.parentElement.offsetWidth);
        if (props.order === 0) {
            setHeight1(((listRef.current.parentElement.clientWidth - 30) / 2 - 30) / 2);
            props.changeHeight(((listRef.current.parentElement.clientWidth - 30) / 2 - 30) + 26)
        }
        else if (props.order === 1) {
            setHeight1((listRef.current.parentElement.clientWidth - 11) / 2);
            props.changeHeight((listRef.current.parentElement.clientWidth - 11) + 12)
        }
    }

    useEffect(() => {
        if (props.order === 2) {
            setHeight1((props.height - 12) / 2);
        }
    }, [props.height])
    return (
        <div className="keen-slider__slide slide1" style={{ height: props.order === 0 ? (height1 * 2 + 26) : (height1 * 2 + 12), minHeight: "inherit" }} ref={listRef}>
            <div className='nftbox-list' >
                {
                    props.order === 0 ?
                        <>
                            <div className='nftbox nftbox1' style={{ height: height1 }}>
                                <img src="/img/765.png" alt="" />
                            </div>
                            <div className='nftbox-list1'>
                                <div className='nftbox nftbox2'>
                                    <img src="/img/478.png" alt="" onLoad={imgLoaded1} ref={imgRef1} />
                                </div>
                                <div className='nftbox nftbox3'>
                                    <img src="/img/14.png" alt="" />
                                </div>
                            </div>
                        </>
                        :
                        (
                            props.order === 1
                                ?
                                <div className='nftbox nftbox1' style={{ height: height1, width: "100%", marginRight: 0 }}>
                                    <img src="/img/765.png" alt="" />
                                </div>
                                :
                                <div className='nftbox-list1' style={{ width: "100%" }}>
                                    <div className='nftbox nftbox2'>
                                        <img src="/img/478.png" alt="" />
                                    </div>
                                    <div className='nftbox nftbox3'>
                                        <img src="/img/14.png" alt="" />
                                    </div>
                                </div>
                        )
                }
            </div>
            <div className='nftbox-list'>
                {
                    props.order === 0 ?
                        <>
                            <div className='nftbox-list1'>
                                <div className='nftbox nftbox4'>
                                    <img src="/img/728.png" alt="" />
                                </div>
                                <div className='nftbox nftbox5'>
                                    <img src="/img/73.png" alt="" />
                                </div>
                            </div>
                            <div className='nftbox nftbox6' style={{ height: height1 }}>
                                <img src="/img/766.png" alt="" ref={imgRef} onLoad={imgLoaded} />
                                <div className='text' style={{ height: height }}>
                                    "The Mandrills" are Crafted from the Minerals with help of mother nature on the Flare Network's lands. Caring community with multiple missions in mind.
                                </div>
                            </div>
                        </>
                        :
                        (
                            props.order === 2 ?
                                <div className='nftbox nftbox6' style={{ height: height1, width: "100%", marginLeft: 0 }}>
                                    <img src="/img/766.png" alt="" ref={imgRef} onLoad={imgLoaded} />
                                    <div className='text' style={{ height: height }}>
                                        "The Mandrills" are Crafted from the Minerals with help of mother nature on the Flare Network's lands. Caring community with multiple missions in mind.
                                    </div>
                                </div>
                                :
                                <div className='nftbox-list1' style={{ width: "100%" }}>
                                    <div className='nftbox nftbox4'>
                                        <img src="/img/728.png" alt="" onLoad={imgLoaded1} ref={imgRef1} />
                                    </div>
                                    <div className='nftbox nftbox5' style={{ marginRight: 0 }}>
                                        <img src="/img/73.png" alt="" />
                                    </div>
                                </div>
                        )
                }
            </div>
        </div>
    )
};

export default Slider1;