import React, { useState, useEffect } from "react"
import { useKeenSlider } from "keen-slider/react"
import "keen-slider/keen-slider.min.css"
import "./styles.css"
import Slider1 from "./Slider1"
import YouTube, { YouTubeProps } from 'react-youtube';

let videoElement = null;
const JourneySlider = () => {
    const [currentSlide, setCurrentSlide] = useState(0)
    const [loaded, setLoaded] = useState(false)
    const [isPlaying, setIsPlaying] = useState(false)
    const [opts, setOpts] = useState({
        height: '100%',
        width: '100%'
    })
    const [sliderRef, instanceRef] = useKeenSlider({
        initial: 0,
        slideChanged(slider) {
            setCurrentSlide(slider.track.details.rel)
        },
        created() {
            setLoaded(true)
        },
    })
    const [height, setHeight] = useState(0)
    const [deviceType, setDeviceType] = useState(0)

    const changeHeight = (hei) => {
        setHeight(hei);
    }

    const _onReady = (event) => {
        videoElement = event;
    };

    useEffect(() => {
        if (
            /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini|Windows Phone/i.test(
                navigator.userAgent
            )
        ) {
            setDeviceType(1);
        } else {
            setDeviceType(0);
        }
    }, []);

    useEffect(() => {
        if (deviceType === 1) {
            instanceRef && instanceRef.current.update()
        }
    }, [deviceType])

    return (
        <>{
            deviceType === 0 ?
                <div className="navigation-wrapper" id="nfts">
                    {/* {loaded && instanceRef.current && ( */}
                    {/* <> */}
                    <div className="slider-arrow" style={{ height: height }} onClick={(e) => {
                        videoElement.target.pauseVideo();
                        e.stopPropagation() || instanceRef.current?.prev()
                    }
                    }>
                        <Arrow
                            left
                            disabled={currentSlide === 0}
                        />
                    </div>
                    {/* </> */}
                    {/* )} */}
                    <div ref={sliderRef} className="keen-slider" >
                        <Slider1 changeHeight={changeHeight} order={0} />
                        <div className="keen-slider__slide slide3" style={{ height: height }}>
                            <YouTube videoId="UofFvV41iZA" opts={opts} onReady={_onReady} />
                        </div>
                    </div>
                    {/* {loaded && instanceRef.current && ( */}
                    {/* <> */}
                    <div className="slider-arrow" style={{ height: height }} onClick={(e) => {
                        if (currentSlide === instanceRef?.current?.track.details.slides.length - 2) {
                            videoElement.target.playVideo();
                        }
                        e.stopPropagation() || instanceRef.current?.next()
                    }
                    }>
                        <Arrow
                            disabled={
                                currentSlide ===
                                instanceRef?.current?.track.details.slides.length - 1
                            }
                        />
                    </div>
                    {/* </> */}
                    {/* )} */}
                </div>
                :
                <div className="navigation-wrapper" id="nfts">
                    {/* {loaded && instanceRef.current && ( */}
                    {/* <> */}
                    <div className="slider-arrow" style={{ height: height }} onClick={(e) => {
                        e.stopPropagation() || instanceRef.current?.prev()
                        videoElement.target.pauseVideo();
                    }
                    }>
                        <Arrow
                            left
                            disabled={currentSlide === 0}
                        />
                    </div>
                    {/* </> */}
                    {/* )} */}
                    <div ref={sliderRef} className="keen-slider" style={{ height: height }}>
                        <Slider1 changeHeight={changeHeight} order={1} height={0} />
                        <Slider1 changeHeight={changeHeight} order={2} height={height} />
                        <div className="keen-slider__slide slide3" style={{ height: height }}>
                            <YouTube videoId="UofFvV41iZA" opts={opts} onReady={_onReady} />
                        </div>
                    </div>
                    {/* {loaded && instanceRef.current && ( */}
                    {/* <> */}
                    <div className="slider-arrow" style={{ height: height }} onClick={(e) => {
                        if (currentSlide === instanceRef?.current?.track.details.slides.length - 2) {
                            videoElement.target.playVideo();
                        }
                        e.stopPropagation() || instanceRef.current?.next()

                    }
                    }>
                        <Arrow
                            disabled={
                                currentSlide ===
                                instanceRef?.current?.track.details.slides.length - 1
                            }
                        />
                    </div>
                    {/* </> */}
                    {/* )} */}
                </div>
        }
            {/* {loaded && instanceRef.current && (
                <div className="dots">
                    {[
                        ...Array(instanceRef.current.track.details.slides.length).keys(),
                    ].map((idx) => {
                        return (
                            <button
                                key={idx}
                                onClick={() => {
                                    instanceRef.current?.moveToIdx(idx)
                                }}
                                className={"dot" + (currentSlide === idx ? " active" : "")}
                            ></button>
                        )
                    })}
                </div>
            )} */}
        </>
    )
}

function Arrow(props) {
    const disabeld = props.disabled ? " arrow--disabled" : ""
    return (
        <svg
            onClick={props.onClick}
            className={`arrow ${props.left ? "arrow--left" : "arrow--right"
                } ${disabeld}`}
            xmlns="http://www.w3.org/2000/svg"
            width="24.614" height="41.309" viewBox="0 0 24.614 41.309"
        >
            {props.left && (
                <g id="Group_91" data-name="Group 91" transform="translate(24.614 41.309) rotate(180)">
                    <path id="Icon_ionic-ios-arrow-back" data-name="Icon ionic-ios-arrow-back" d="M7.119,20.649,22.75,5.031A2.952,2.952,0,0,0,18.568.862L.86,18.559a2.946,2.946,0,0,0-.086,4.071L18.556,40.449a2.952,2.952,0,1,0,4.181-4.169Z" transform="translate(24.614 41.309) rotate(180)" fill="#fff" />
                    <path id="Icon_ionic-ios-arrow-back-2" data-name="Icon ionic-ios-arrow-back" d="M3.5,10.152l7.685-7.679A1.451,1.451,0,0,0,9.129.424L.423,9.124a1.449,1.449,0,0,0-.042,2l8.743,8.761a1.451,1.451,0,1,0,2.056-2.05Z" transform="translate(11.611 30.809) rotate(180)" fill="#fff" />
                </g>
            )}
            {!props.left && (
                <g id="Group_93" data-name="Group 93" >
                    <path id="Icon_ionic-ios-arrow-back" data-name="Icon ionic-ios-arrow-back" d="M7.119,20.649,22.75,5.031A2.952,2.952,0,0,0,18.568.862L.86,18.559a2.946,2.946,0,0,0-.086,4.071L18.556,40.449a2.952,2.952,0,1,0,4.181-4.169Z" transform="translate(24.614 41.309) rotate(180)" fill="#fff" />
                    <path id="Icon_ionic-ios-arrow-back-2" data-name="Icon ionic-ios-arrow-back" d="M3.5,10.152l7.685-7.679A1.451,1.451,0,0,0,9.129.424L.423,9.124a1.449,1.449,0,0,0-.042,2l8.743,8.761a1.451,1.451,0,1,0,2.056-2.05Z" transform="translate(11.611 30.809) rotate(180)" fill="#fff" />
                </g>
            )}
        </svg>
    )
}

export default JourneySlider;