import React, { useEffect, useState } from "react";
import './loading.scss';

const Loading = () => {
    const [loadingText, setLoadingText] = useState("Loading.");

    React.useEffect(() => {
        const timer = window.setInterval(() => {
            setLoadingText(prevTime => {
                if (prevTime === "Loading.")
                    return "Loading.."
                else if (prevTime === "Loading..")
                    return "Loading..."
                else
                    return "Loading."
            }); // <-- Change this line!
        }, 500);
        return () => {
            window.clearInterval(timer);
        };
    }, []);

    const setText = () => {
        if (loadingText === "Loading.")
            setLoadingText("Loading..")
        else if (loadingText === "Loading..")
            setLoadingText("Loading...")
        else
            setLoadingText("Loading.")
    }
    return (
        <div className="Loading">
            <div className="title">
                tHE MANDRILLS
            </div>
            <div className="loadingImg">
                <img src="/img/loading.png" alt="loading" />
                <div className="loadingText">{loadingText}</div>
            </div>
        </div>
    )
}

export default Loading;